const tabs = (node) => {
  if (!node) return false;
  const links = [...node.querySelectorAll('[data-tab]')];
  if (!links) return false;
  const activeLink = links.filter(link => link.matches('.is-active'));
  const tabsArr = [...node.querySelectorAll('[data-tab-id]')];

  function toggleTab(id) {
    links.forEach((link) => {
      const target = link;
      if (target.dataset.tab === id) {
        target.classList.add('is-active');
      } else {
        target.classList.remove('is-active');
      }
    });
    tabsArr.forEach((tab) => {
      const target = tab;
      if (target.dataset.tabId === id) {
        target.style.display = '';
      } else {
        target.style.display = 'none';
      }
    });
  }
  if (activeLink.length) {
    toggleTab(activeLink[0].dataset.tab);
  } else {
    toggleTab(links[0].dataset.tab);
  }
  node.addEventListener('click', (event) => {
    if (event.target.dataset && event.target.dataset.tab) {
      toggleTab(event.target.dataset.tab);
      event.preventDefault();
    }
  });
  return tabsArr;
};

export default tabs;
